@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap);
* {
  box-sizing: border-box;
}
body {
  font-family: "Lexend Deca", sans-serif;
  background-color: #efefef;
}
.nav-list {
  align-items: center;
  justify-content: center;
  grid-gap: 20%;
  gap: 20%;
  font-size: 15px;
  font-weight: 1000;
  display: flex;
}
.nav-list ul {
  list-style: none;
  margin-top: 2%;
}
.nav-list li a {
  text-decoration: none;
  color: black;
}

.light {
  color: #727070;
}

.text-style {
  height: 80vh;
  align-items: center;
  justify-content: center;
  font-size: 120px;
  display: flex;
  font-family: "Lexend Deca", sans-serif;
}
.burger {
  display: none;
  position: relative;
  cursor: pointer;
  left: 0%;
  top: 0%;
}
.line {
  width: 33px;
  background-color: black;
  height: 4px;
  margin: 3px 3px;
}

@media only screen and (max-width: 850px) {
  .burger {
    display: block;
    z-index: 50;
    background: 0;
    border: 0;
    top: 3%;
    position: fixed;
  }

  .nav-list {
    flex-direction: column;
    top: 0;
    left: 0;

    padding-top: 20%;
    padding-left: 1%;
    font-size: large;
    height: 10vh;

    grid-gap: 30px;

    gap: 30px;
  }

  .slide {
    position: absolute;

    box-shadow: 0px 0px 30px;
    -webkit-transform: translateX(-120%);
            transform: translateX(-120%);
    transition: all 300ms ease-in-out;
    background-color: brown;
    height: 100%;
    width: 25%;
    top: 0%;
  }
  .show {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}
@media screen and (max-width: 600px) {
  .nav-list {
    padding-top: 25%;
  }
  .slide {
    width: 30%;
  }
}
@media screen and(max-height:650px) {
  .nav-list {
    grid-gap: 20px;
    gap: 20px;
    font-size: medium;
  }
}

